.app-store_btn {
    display: flex;
    justify-content: center;
    align-items: center;

    background: rgba(0, 0, 0, 1);
    color: #ffffff;
    height: 48px;
    border: none;
    border-radius: 40px;
}

.app-store_btn:hover {
    opacity: 0.6;
}

.app-store_btn img {
    margin-right: 12px;
}

.app-store_btn span {
    font-family: Raleway;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 19px;
}


.google-play_btn {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    background: rgba(0, 0, 0, 1);
    color: #ffffff;
    height: 48px;
    border: none;
    border-radius: 40px;
}

.google-play_btn:hover {
    opacity: 0.6;
}

.google-play_btn img {
    margin-right: 12px;
}

.google-play_btn span {
    font-family: Raleway;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 19px;
}
.header-frame {
    display: none;
    position: absolute;
    width: 252px;
    height: 384px;
    right: 1px;
    top: 100px;
    padding: 40px 40px;
    z-index: 100;

    border: 1px solid;
    background: rgba(255, 255, 255);
    border-image-source: linear-gradient(180deg, #FFFFFF 0%, #FFFFFF 100%);
    border-radius: 10px;
}

.header-frame_link {
    margin-bottom: 20px;
}

.header-frame a {
    font-family: Raleway Regular;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 23px;

    color: #000000;
    text-decoration: none;
}

.header-frame .app-store_btn {
    width: 150px;
    margin-bottom: 20px;
}

.header-frame .google-play_btn {
    width: 150px;
}

.active-frame_link {
    font-weight: bold;
}
.header {
    position: fixed;
    width: 100%;
    max-width: 1920px;
    margin: 0 auto;
    z-index: 1000;
}

.header .container {
    max-width: 1218px;
}

.activeClass {
    display: block;
}

.navbar_menu {
    display: flex;
    justify-content: space-between;
    padding: 30px 0;
}

.hamburger {
    display: none;
    position: absolute;
    right: 25px;
    top: 34px;
    width: 36px;
    height: 36px;
    cursor: pointer;
}

.navbar_menu_item {
    display: flex;
    align-items: center;
}

.logo {
    margin-right: 100px;
}

.navbar_links a {
    display: flex;
    align-items: center;
    text-decoration: none;
    color: #000000;

    font-family: Raleway;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 21px;
}

.navbar_links {
    display: flex;
    margin-bottom: 0;
}

.navbar_link {
    margin-right: 64px;
}

.navbar_link:last-child {
    margin-right: 0;
}

.header_btn .app-store_btn, .header .google-play_btn {
    width: 150px;
}

.header_btn .app-store_btn {
    margin-right: 20px;
}

.header .google-play_btn {
    margin-right: 64px;
}

.language {
    display: flex;
    align-items: center;

    font-family: Raleway;
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 23px;
    text-decoration: none;
    color: #000000;
}

.language:hover {
    color: #000000;
    text-decoration: none;
}

@media (max-width: 1920px) {
    .header_btn .app-store_btn, .header_btn .google-play_btn {
        display: none;
    }
}

@media (max-width: 1440px) {
    .navbar_links {
        display: none;
    }
    .language {
        position: absolute;
        right: 80px;
        top: 38px;
    }
    .hamburger {
        display: block;
    }
}


